.page-container {
  display: flex;
  width: 80%;
  flex-direction: column;
  top: 64px;
  position: relative;
}
.page-container .parent-footer .footer-copyrights {
  padding-left: 16px;
}
@media (min-width: 0px) {
  .signin-footer {
    background-color: #ffffff;
    float: right;
    height: 60px;
    bottom: 0;
    position: fixed;
    z-index: 111;
    width: 100%;
  }
  .forgotpassword-btn {
    border-color: #ffffff;
    height: 40px;
    width: 145px;
    font-family: "AvenirNext-Medium";
  }
  .signin-btn {
    color: #32b9d1;
    border-color: #32b9d1;
    height: 40px;
    width: 88px;
    font-family: "AvenirNext-Medium";
  }
  .signin-btn:hover {
    background-color: rgba(255, 255, 255, 0.8);
  }
}
@media (min-width: 768px) and (max-width: 992px) {
  .page-container {
    width: 40%;
  }
}
@media (min-width: 993px) {
  .page-container {
    width: 40%;
  }
}
@media (min-width: 1200px) {
  .page-container {
    width: 40%;
  }
}
@media (min-width: 1600px) {
  .page-container {
    width: 40%;
  }
}
